import React from "react";
import {
  MDBCarousel,
  MDBCarouselInner,
  MDBCarouselItem,
  MDBView
} from "mdbreact";
import CamaroteCard from "../components/CamaroteCard.jsx";

class CamaroteCarousel extends React.Component {
  constructor(props) {
    super(props);
    this.camarotes = [
      {
        id: 1,
        nome: "Camarote Skol",
        foto:
          "https://foliaproducoes.com.br/wp-content/uploads/2019/09/Skol.jpg",
        censura: 18,
        preco: 500,
        modalidade: "open bar"
      },
      {
        id: 2,
        nome: "Camarote Club",
        foto:
          "https://foliaproducoes.com.br/wp-content/uploads/2019/08/Club.jpg",
        censura: 18,
        preco: 5000,
        modalidade: "all inclusive"
      },
      {
        id: 3,
        nome: "Carnavalito",
        foto:
          "https://foliaproducoes.com.br/wp-content/uploads/2019/08/carnavalito.jpg",
        censura: 18,
        preco: 500,
        modalidade: "open bar"
      },
      {
        id: 4,
        nome: "Nana",
        foto:
          "https://foliaproducoes.com.br/wp-content/uploads/2019/08/Nana.jpg",
        censura: 18,
        preco: 500,
        modalidade: "open bar"
      },
      {
        id: 5,
        nome: "Villamix",
        foto:
          "https://foliaproducoes.com.br/wp-content/uploads/2019/02/Vilamix-1.jpg",
        censura: 18,
        preco: 500,
        modalidade: "open bar"
      },
      {
        id: 6,
        nome: "Salvador",
        foto:
          "https://foliaproducoes.com.br/wp-content/uploads/2018/09/Salvador.jpg",
        censura: 18,
        preco: 500,
        modalidade: "open bar"
      }
    ];
  }
  render() {
    return (
      <MDBCarousel
        activeItem={1}
        length={this.camarotes.length}
        showControls={true}
        className="z-depth-1"
        interval={false}
        slide
      >
        <MDBCarouselInner>
          {this.camarotes.map(camarote => (
            <MDBCarouselItem key={camarote.id} itemId={camarote.id}>
              <MDBView>
                <CamaroteCard camarote={camarote} />
              </MDBView>
            </MDBCarouselItem>
          ))}
        </MDBCarouselInner>
      </MDBCarousel>
    );
  }
}

export default CamaroteCarousel;
