import React from "react";
import {
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBCardTitle,
  MDBCardText
} from "mdbreact";

class CamaroteCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = { camarote: {} };
  }
  componentDidMount() {
    this.setState({ camarote: this.props.camarote });
  }
  render() {
    return (
      <MDBCard style={{ width: "22rem" }}>
        <MDBCardImage
          className="img-fluid"
          src={this.state.camarote.foto}
          waves
        />
        <MDBCardBody>
          <MDBCardTitle>{this.state.camarote.nome}</MDBCardTitle>
          <MDBCardText>
            {this.state.camarote.censura} - {this.state.camarote.modalidade} -{" "}
            {this.state.camarote.preco}
          </MDBCardText>
        </MDBCardBody>
      </MDBCard>
    );
  }
}

export default CamaroteCard;
